.node {
  width: 25px;
  height: 25px;
  outline: 1px solid rgb(175, 216, 248);
  display: inline-table;
}

@media (max-width: 776px) {
  .node {
    width: 8px;
    height: 8px;
  }

  .node-wall {
    background-color: rgb(12, 53, 71);
  }
}

@media (min-width: 776px) {
  .node-wall {
    background-color: rgb(12, 53, 71);
    outline: rgb(12, 53, 71);
  }
}

.node-finish {
  background-color: white;
  background-image: url(../../assets/finish-line.png);
  background-size: cover;
}

.node-start {
  background-color: white;
  background-image: url(../../assets/running-man.png);
  background-size: cover;
}

.node-visited {
  animation-name: visitedAnimation;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes visitedAnimation {
  0% {
    transform: scale(0.3);
    background-color: rgba(0, 0, 66, 0.75);
    border-radius: 100%;
  }

  50% {
    background-color: rgba(17, 104, 217, 0.75);
  }

  75% {
    transform: scale(1.2);
    background-color: rgba(0, 217, 159, 0.75);
  }

  100% {
    transform: scale(1);
    background-color: rgba(0, 190, 218, 0.75);
  }
}

.node-animated-wall {
  animation-name: wallAnimation;
  animation-duration: 0.25s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes wallAnimation {
  0% {
    transform: scale(0.4);
    background-color: rgb(12, 53, 71);
    outline: rgb(12, 53, 71);
    border-radius: 30%;
  }

  50% {
    transform: scale(0.6);
    background-color: rgb(12, 53, 71);
    outline: rgb(12, 53, 71);
    border-radius: 20%;
  }

  75% {
    transform: scale(0.8);
    background-color: rgb(12, 53, 71);
    outline: rgb(12, 53, 71);
    border-radius: 10%;
  }

  100% {
    transform: scale(1);
    background-color: rgb(12, 53, 71);
    outline: rgb(12, 53, 71);
    /* border: 1px solid rgb(2, 36, 51); */
  }
}

.node-shortest-path {
  animation-name: shortestPath;
  animation-duration: 1.5s;
  animation-timing-function: ease-out;
  animation-delay: 0;
  animation-direction: alternate;
  animation-iteration-count: 1;
  animation-fill-mode: forwards;
  animation-play-state: running;
}

@keyframes shortestPath {
  0% {
    transform: scale(0.6);
    background-color: rgb(250, 181, 93);
  }

  50% {
    transform: scale(1.2);
    background-color: rgb(255, 177, 60);
  }

  100% {
    transform: scale(1);
    background-color: rgb(255, 124, 0);
  }
}
